import React, { Component }from 'react';
import './Buttons.css'
import Display from './Display';
import Input from './Input';


class Buttons extends Component {
    state = { register: '0', input: 0, disabled: false, eqdis: false}

    

// +
// if current register and input = 0, just show +
// if current register and input = - or + or % or X
    acHandler() {
        this.setState({ register: '0', input: 0, disabled: false})
    }
    plusHandler() {
        const currentRegister = this.state.register;
        const currentInput = this.state.input;
        const newRegister = currentRegister.substring(0, currentRegister.length -1)
        if (currentRegister.slice(-1) === '-' || currentRegister.slice(-1) === '0' || currentRegister.slice(-1) === '+'||currentRegister.slice(-1) === '/' || currentRegister.slice(-1) === '*'){
            this.setState({ register: newRegister + '+', input: '+'})
        } else if (currentRegister.indexOf('=') >= 0){
            this.setState({ register: currentInput + '+', input: '+', disabled: false, eqdis: true})
        }else {
            this.setState({ register: currentRegister + "+", input: "+", disabled: false, eqdis: true})
        }
    }
    minusHandler() {
        const currentRegister = this.state.register;
        const currentInput = this.state.input;
        const newRegister = currentRegister.substring(0, currentRegister.length -1)
        if (currentRegister.slice(-1) === '-' || currentRegister.slice(-1) === '0' || currentRegister.slice(-1) === '+'||currentRegister.slice(-1) === '/' || currentRegister.slice(-1) === '*'){
            this.setState({ register: newRegister + '-', input: '-'})
        } else if (currentRegister.indexOf('=') >= 0){
            this.setState({ register: currentInput + '-', input: '-', disabled: false, eqdis: true})
        } else {
            this.setState({ register: currentRegister + "-", input: "-", disabled: false, eqdis: true})
        }
    }
    
    timesHandler() {
        const currentRegister = this.state.register;
        const currentInput = this.state.input;
        const newRegister = currentRegister.substring(0, currentRegister.length -1)
        if (currentRegister.slice(-1) === '-' || currentRegister.slice(-1) === '0' || currentRegister.slice(-1) === '+'||currentRegister.slice(-1) === '/' || currentRegister.slice(-1) === '*'){
            this.setState({ register: newRegister + '*', input: 'x'})
        } else if (currentRegister.indexOf('=') >= 0){
            this.setState({ register: currentInput + '*', input: 'x', disabled: false, eqdis: true})
        } else {
            this.setState({ register: currentRegister + '*', input: 'x', disabled: false, eqdis: true})
        }
    }
    divideHandler() {
        const currentRegister = this.state.register;
        const currentInput = this.state.input;
        const newRegister = currentRegister.substring(0, currentRegister.length -1)
        if (currentRegister.slice(-1) === '-' || currentRegister.slice(-1) === '0' || currentRegister.slice(-1) === '+'||currentRegister.slice(-1) === '/' || currentRegister.slice(-1) === '*'){
            this.setState({ register: newRegister + '/', input: '/'})
        } else if (currentRegister.indexOf('=') >= 0){
            this.setState({ register: currentInput + '/', input: '/', disabled: false, eqdis: true})
        } else {
            this.setState({ register: currentRegister + '/', input: '/', disabled: false, eqdis: true})
        }
    }
    equalHandler() {
        const currentRegister = this.state.register;
        const inpLength = this.state.length + 1;
        const disDec = currentRegister.slice(currentRegister.length - inpLength)
        this.setState({ register: currentRegister + "=" + eval(currentRegister), input: eval(currentRegister), eqdis: true})
        if (disDec.indexOf('.') >=0) {
            this.setState({ disabled: true})
        } else {
            this.setState({ disabled: false})
        }
    }
    // 1
    oneHandler() {
        const currentRegister = this.state.register;
        const currentInput = this.state.input;
        if (currentInput === 0) {
            this.setState({ register: '1', input: 1})
        } else if (currentRegister.indexOf('=') >= 0){
            this.setState({ register: currentInput + '1', input: '1', disabled: false, eqdis: false})
        } else if (currentInput === '+' || currentInput === '-' || currentInput === '/' || currentInput === 'x') {
            this.setState({ register: currentRegister + '1', input: 1})
        } else {
            this.setState({ register: currentRegister + '1', input: currentInput.toString() + '1', eqdis: false})
        }
    }

    twoHandler() {
        const currentRegister = this.state.register;
        const currentInput = this.state.input;
        if (currentInput === 0) {
            this.setState({ register: '2', input: 2, eqdis: false})
        } else if (currentRegister.indexOf('=') >= 0){
            this.setState({ register: currentInput + '2', input: '2', disabled: false, eqdis: false})
        }  else if (currentInput === '+' || currentInput === '-' || currentInput === '/' || currentInput === 'x') {
            this.setState({ register: currentRegister + '2', input: 2, eqdis: false})
        }  else {
            this.setState({ register: currentRegister + '2', input: currentInput.toString() + '2' , eqdis: false})
        }
    }
    threeHandler() {
        const currentRegister = this.state.register;
        const currentInput = this.state.input;
        if (currentInput === 0) {
            this.setState({ register: '3', input: 3, eqdis: false})
        } else if (currentRegister.indexOf('=') >= 0){
            this.setState({ register: currentInput + '3', input: '3', disabled: false, eqdis: false})
        }  else if (currentInput === '+' || currentInput === '-' || currentInput === '/' || currentInput === 'x') {
            this.setState({ register: currentRegister + '3', input: 3, eqdis: false})
        }  else {
            this.setState({ register: currentRegister + '3', input: currentInput.toString() + '3' , eqdis: false})
        }
    }
    fourHandler() {
        const currentRegister = this.state.register;
        const currentInput = this.state.input;
        if (currentInput === 0) {
            this.setState({ register: '4', input: 4, eqdis: false})
        } else if (currentRegister.indexOf('=') >= 0){
            this.setState({ register: currentInput + '4', input: '4', disabled: false, eqdis: false})
        }  else if (currentInput === '+' || currentInput === '-' || currentInput === '/' || currentInput === 'x') {
            this.setState({ register: currentRegister + '4', input: 4, eqdis: false})
        }  else {
            this.setState({ register: currentRegister + '4', input: currentInput.toString() + '4' , eqdis: false})
        }
    }
    fiveHandler() {
        const currentRegister = this.state.register;
        const currentInput = this.state.input;
        if (currentInput === 0) {
            this.setState({ register: '5', input: 5, eqdis: false})
        } else if (currentRegister.indexOf('=') >= 0){
            this.setState({ register: currentInput + '5', input: '5', disabled: false, eqdis: false})
        }  else if (currentInput === '+' || currentInput === '-' || currentInput === '/' || currentInput === 'x') {
            this.setState({ register: currentRegister + '5', input: 5, eqdis: false})
        }  else {
            this.setState({ register: currentRegister + '5', input: currentInput.toString() + '5' , eqdis: false})
        }
    }
    sixHandler() {
        const currentRegister = this.state.register;
        const currentInput = this.state.input;
        if (currentInput === 0) {
            this.setState({ register: '6', input: 6, eqdis: false})
        } else if (currentRegister.indexOf('=') >= 0){
            this.setState({ register: currentInput + '6', input: '6', disabled: false, eqdis: false})
        }  else if (currentInput === '+' || currentInput === '-' || currentInput === '/' || currentInput === 'x') {
            this.setState({ register: currentRegister + '6', input: 6, eqdis: false})
        }  else {
            this.setState({ register: currentRegister + '6', input: currentInput.toString() + '6' , eqdis: false})
        }
    }
    sevenHandler() {
        const currentRegister = this.state.register;
        const currentInput = this.state.input;
        if (currentInput === 0) {
            this.setState({ register: '7', input: 7, eqdis: false})
        } else if (currentRegister.indexOf('=') >= 0){
            this.setState({ register: currentInput + '7', input: '7', disabled: false, eqdis: false})
        }  else if (currentInput === '+' || currentInput === '-' || currentInput === '/' || currentInput === 'x') {
            this.setState({ register: currentRegister + '7', input: 7, eqdis: false})
        }  else {
            this.setState({ register: currentRegister + '7', input: currentInput.toString() + '7' , eqdis: false})
        }
    }
    eightHandler() {
        const currentRegister = this.state.register;
        const currentInput = this.state.input;
        if (currentInput === 0) {
            this.setState({ register: '8', input: 8, eqdis: false})
        } else if (currentRegister.indexOf('=') >= 0){
            this.setState({ register: currentInput + '8', input: '8', disabled: false, eqdis: false})
        }  else if (currentInput === '+' || currentInput === '-' || currentInput === '/' || currentInput === 'x') {
            this.setState({ register: currentRegister + '8', input: 8, eqdis: false})
        }  else {
            this.setState({ register: currentRegister + '8', input: currentInput.toString() + '8', eqdis: false })
        }
    }
    nineHandler() {
        const currentRegister = this.state.register;
        const currentInput = this.state.input;
        if (currentInput === 0) {
            this.setState({ register: '9', input: 9, eqdis: false})
        } else if (currentRegister.indexOf('=') >= 0){
            this.setState({ register: currentInput + '9', input: '9', disabled: false, eqdis: false})
        }  else if (currentInput === '+' || currentInput === '-' || currentInput === '/' || currentInput === 'x') {
            this.setState({ register: currentRegister + '9', input: 9, eqdis: false})
        }  else {
            this.setState({ register: currentRegister + '9', input: currentInput.toString() + '9' , eqdis: false})
        }
    }
    
    zeroHandler() {
        const currentRegister = this.state.register;
        const currentInput = this.state.input;
        if (currentInput === 0) {
            this.setState({ register: '0', input: 0, eqdis: false})
        }  else if (currentInput === '+' || currentInput === '-' || currentInput === '/' || currentInput === 'x') {
            this.setState({ register: currentRegister + '0', input: 0, eqdis: false})
        } else {
            this.setState({ register: currentRegister + '0', input: currentInput.toString() + '0' , eqdis: false})
        }
    }
    pointHandler() {
        const currentRegister = this.state.register;
        const currentInput = this.state.input;
        const inpLength = this.state.length + 1;
        const disDec = currentRegister.slice(currentRegister.length - inpLength)
        if (currentInput === 0) {
            this.setState({ register: '0.', input: '0.', disabled: true})
        } else if (currentInput === '+' || currentInput === '-' || currentInput === '/' || currentInput === 'x') {
            this.setState({ register: currentRegister + '0.', input: '0.',disabled: true})
        } else if(disDec.indexOf('=') >= 0){
            this.setState({register: currentInput + '.', input: currentInput + '.'})
        } else {
            this.setState({ register: currentRegister + '.', input: currentInput.toString() + '.' , disabled: true})
        }
    }
    
    render() {
        const disab = this.state.register;
        return (
            <div>
                <Display {...this.state} />
                <Input {...this.state} />
                
                <button className="ACButton" onClick={this.acHandler.bind(this)}>AC</button>
        <button className="DM"  disabled={disab[0]==='0' || disab[0] === '/' || disab[0] === '+' || disab[0] ==='-' && disab.length <= 1} onClick={this.timesHandler.bind(this)}>x</button>
                <button className="DM"  disabled={disab[0]==='0' || disab[0] === '*' || disab[0] === '+' || disab[0] ==='-' && disab.length <= 1} onClick={this.divideHandler.bind(this)}>/</button>
                <button className="Num" onClick={this.sevenHandler.bind(this)}>7</button>
                <button className="Num" onClick={this.eightHandler.bind(this)}>8</button>
                <button className="Num" onClick={this.nineHandler.bind(this)}>9</button>
                <button className="DM"  onClick={this.minusHandler.bind(this)}> - </button>
                <button className="Num" onClick={this.fourHandler.bind(this)}>4</button>
                <button className="Num" onClick={this.fiveHandler.bind(this)}>5</button>
                <button className="Num" onClick={this.sixHandler.bind(this)}>6</button>
                <button className="DM" disabled={disab.slice(-1)==='+'} 
                        onClick={this.plusHandler.bind(this)}> + </button>
                <button className="Num" onClick={this.oneHandler.bind(this)}>1</button>
                <button className="Num" onClick={this.twoHandler.bind(this)}>2</button>
                <button className="Num" onClick={this.threeHandler.bind(this)}>3</button>
                <button className="Zero" onClick={this.zeroHandler.bind(this)}>0</button>
                <button className="Num" disabled={this.state.disabled} onClick={this.pointHandler.bind(this)}> . </button>
                <button className="Equal" disabled={this.state.eqdis}
                        onClick={this.equalHandler.bind(this)}>=</button>
            </div>
        );
    }
}

export default Buttons;


